import React from "react";
import Navigation from "../components/Navigation";
import Mouse from "../components/Mouse";
import SocialNetwork from "../components/SocialNetwork";
import DynamicText from "../components/DynamicText";
import Button from "../components/Button";
import { motion } from "framer-motion";

const Home = () => {
    const variants = {
        initial: {
            opacity: 0,
            transition: { duration: 0.5 },
            x: 100,
        },
        visible: {
            opacity: 1,
            x: 0,
        },
        exit: {
            opacitu: 0,
            transition: { duration: 0.3 },
            x: -100,
        },
    };
    return (
        <div>
            <Mouse />
            <motion.div
                className="home"
                initial="initial"
                animate="visible"
                exit="exit"
                variants={variants}
            >
                <Navigation />
                <SocialNetwork />
                <div className="home-main">
                    <div className="main-content">
                        <motion.h1
                            drag
                            onDragEnd
                            dragConstraints={{
                                left: -250,
                                right: 950,
                                top: -200,
                                bottom: -200,
                            }}
                        >
                            Maxime GUIRAUD
                        </motion.h1>
                        <motion.h2
                            drag
                            onDragEnd
                            dragConstraints={{
                                left: -250,
                                right: 950,
                                top: -200,
                                bottom: -200,
                            }}
                        >
                            <DynamicText />
                        </motion.h2>
                    </div>
                </div>
                <Button right={"/CV"} />
                {/* on lui passe la props /projet-1 */}
            </motion.div>
        </div>
    );
};

export default Home;
